<template lang="pug">
	.modal-container.calendar-modal-container
		.modal-header
			.icon.icon-modal-close(@click="$emit('close')")
				img(src="@/assets/img/icons/modal/modal-close.svg")
			.modal-header__title Дата
			.icon.icon-modal-check(@click="$emit('close')")
				img(src="@/assets/img/icons/modal/modal-check.svg")

		.modal-body
			v-date-picker(
				title-position="left"
				is-expanded
				:locale="{ masks: { weekdays: 'WW' } }"
				mode="date" 
				v-model="currentDate"
			)
		.modal-footer
			.calendar-modal-menu
				.calendar-modal-menu__item(@click="currentDate = ''")
					.calendar-modal-menu__icon
						.icon.icon-clear(
							@click="clearCurrentDate")
								img(src="@/assets/img/icons/modal/modal-search-x.svg", alt="")
			
					.calendar-modal-menu__text Удалить дату

				.calendar-modal-menu__item
					.calendar-modal-menu__icon
						.icon.icon-bell
								img(src="@/assets/img/icons/modal/bell.svg", alt="")
			
					.calendar-modal-menu__text Напомнить

			//- button(@click="currentDate = ''") Reset date

</template>

<script>


	export default{
		props: ['currentDate'],
		name: 'CalendarModal',
		// props: ['selectedMeeting'],
		data(){
			return{
			}

		},
		components: {
			// Calendar
		},
		methods: {
			clearCurrentDate(){

			}

		}
	}
</script>

<style lang="scss">
	@import '@/assets/scss/misc/_vars.scss';
	@import '@/assets/scss/misc/_mixins.scss';

	// v-calendar customization
	
	.vc-container{
		border: none;
		overflow: hidden;
	}
	.vc-header{
		margin-bottom: 15px;
		padding-left: 24px !important;
		padding-right: 24px !important;
		// padding: 10px 24px 0 24px;
	}
	.vc-arrow{
		width: 26px;
		height: 26px;
		background-repeat: no-repeat;
		background-position: center center;

		.vc-svg-icon{
			display: none;
		}
		

		&.is-left{
			background-image: url(~@/assets/img/icons/modal/calendar-prev.svg);
		}
		&.is-right{
			background-image: url(~@/assets/img/icons/modal/calendar-next.svg);
		}
		&:hover, &:active, &:focus{
			background-repeat: no-repeat;
			background-position: center center;
			background-color: transparent;
		}
		
	}
	.vc-title{
		padding-right: 15px;
		position: relative;
		font-size: 13px !important;
		font-family: $sf-t-semi;
		font-weight: normal !important;


		&:after{
			content: '';
			position: absolute;
			right: 0;
			top: 50%;
			width: 0;
			transform: translateY(-50%);
			height: 0;
			border-style: solid;
			border-width: 5px 4.5px 0 4.5px;
			border-color: #151522 transparent transparent transparent;
		}
	}
	.vc-weeks{
		margin: 0 -7px;
	}
	.vc-weekday{
		font-size: 11px !important;
		font-family: $sf-t-reg;
		color: #666666;
		text-transform: uppercase;
		font-weight: normal !important;
	}
	.vc-day{
		min-height: 34px !important; 
	}
	.vc-day-content{
		font-size: 15px !important;
	}
	.vc-highlights ~ .vc-day-content{
		background-color: $link !important;
		font-weight: normal !important;
	}
	// END v-calendar customization

	.calendar-modal-menu__item{
		display: flex;
		height: 48px;
		align-items: center;
		padding: 0 20px;
		border-top: 1px solid rgba(228, 228, 228, 0.6);

		&:active, &:focus{
			background-color: $l-gray;
		}
		
	}
	.calendar-modal-menu__icon{
		flex-shrink: 0;
		width: 20px;
		height: 23px;
		margin-right: 12px;
		position: relative;

		.icon{
			@include absolute-center;
		}
		.icon-clear{
			right: auto;
			margin-top: -2px;
		}
	}
	.icon-bell{
		width: 20px;
		height: 23px;
	}
	.calendar-modal-menu__text{

	}
</style>
